<template>
	<div id="goods-video">
		<public-header :title="goodsActive.title" v-if="!util.isWeixin()"></public-header>
		<public-footer :menufooter="menufooter"></public-footer>
		<div class="content">
			<van-swipe
				class="goods-swipe"
				:duration="500"
				:initial-swipe="goodsIndex"
				:show-indicators="false"
				:vertical="true"
				:loop="false"
				:lazy-render="true"
				@change="onSwipeChange"
			>
				<van-swipe-item v-for="(item, index) in videoList" :key="index">
					<template v-if="index >= goodsIndex - 1 && index <= goodsIndex + 1">
						<template v-if="item.showType == 'video'">
							<video-player class="video-player vjs-custom-skin"
										  :ref="`videoPlayer-${index}`"
										  :playsinline="true"
										  :options="item.playerOptions"
										  @ready="onPlayerReady(index)"
							>
							</video-player>
						</template>
						<template v-else-if="item.showType == 'slides'">
							<van-swipe class="slide-swipe" :autoplay="1500" :show-indicators="false">
								<van-swipe-item v-for="(slideItem, slideIndex) in item.slides" :key="slideIndex">
									<van-image
											fit="contain"
											:src="slideItem"
									/>
								</van-swipe-item>
							</van-swipe>
						</template>
						<template v-else-if="item.showType == 'thumb'">
							<div class="thumb-wrap">
								<van-image
										fit="contain"
										:src="item.thumb"
								/>
							</div>
						</template>
					</template>
				</van-swipe-item>
			</van-swipe>
			<div class="goods-info">
				<div class="goods-title">{{goodsActive.title}}</div>
				<div class="sailed">已售：{{goodsActive.sailed}} 好评：{{goodsActive.comment_good_percent}}</div>
				<div class="goods-desc" v-if="goodsActive.content">{{goodsActive.content}}</div>
				<div class="price">
					<div class="now-price">
						<text class="c-danger">{{Lang.dollarSign}}</text>
						<text class="font-bold font-18 c-danger">{{goodsActive.price}}</text>
					</div>
					<div class="old-price" v-if="goodsActive.old_price">
						<text>{{Lang.dollarSign}}</text>
						<text>{{goodsActive.old_price}}</text>
					</div>
				</div>
			</div>
			<div class="btn-group">
				<div class="store-logo">
					<div class="logo" @click="util.jsUrl('/pages/store/goods', {sid: store.id})">
						<img :src="store.logo" />
					</div>
					<div class="icon icon-roundaddfill" @click="onFavor" v-if="!store.is_favorite"></div>
				</div>
				<div class="btn-item" @click="util.jsUrl('/pages/store/goodsDetail', {sid: store.id, id: goodsActive.id})">
					<div class="icon icon-commentfill"></div>
					<div class="btn-text">{{goodsActive.comment_total}}</div>
				</div>
				<div class="btn-item" @click="onToggleSharePopupStatus">
					<div class="icon icon-forwardfill"></div>
					<div class="btn-text">分享</div>
				</div>
				<div class="btn-item" @click="util.jsUrl('/pages/order/create', {sid: store.id})">
					<div class="icon icon-cartfill"></div>
					<div class="btn-text">去结算</div>
					<div class="goods-num" v-if="cart.num > 0">{{cart.num}}</div>
				</div>
			</div>
			<goods-handle
				:goods="goodsActive"
				:optionId="0"
				:showAnimate="false"
				from="detail"
			>
			</goods-handle>
		</div>
		<!-- 购物车部分 -->
		<store-cart
			:show="true"
			:store="store"
			:pindan_id="pindan_id"
			@selectPinadan="selectPinadan" v-if="0">
		</store-cart>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
		<i-van-share-sheet
			:status="sharePopupStatus"
			title="立即分享给好友"
			:query="{type: 1, id: goodsActive.id}"
			@onTogglePopup="onToggleSharePopupStatus"
		>
		</i-van-share-sheet>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	import StoreCart from '@/components/storeCart'
	import GoodsHandle from '@/components/goodsHandle'
	import IVanShareSheet from '@/components/iVanShareSheet'
	import { videoPlayer } from 'vue-video-player'
	import 'vue-video-player/src/custom-theme.css'
	import 'video.js/dist/video-js.css'
	export default {
		data() {
			return {
				cart: {},
				store: {},
				goodsActive: {
					title: '商品详情'
				},
				videoList: [],
				goodsIndex: 0,
				_last: 0,
				sharePopupStatus: false,
				showPreLoading: true,
				menufooter: {},
			}
		},
		components: {
			PublicHeader,
			StoreCart,
			GoodsHandle,
			IVanShareSheet,
			PublicFooter,
			videoPlayer
		},
		methods: {
			...mapMutations([
				'replaceStore', 'replaceCart'
			]),
			onToggleSharePopupStatus() {
				var that = this;
				that.sharePopupStatus = !that.sharePopupStatus;
			},
			selectPinadan(pindan_id) {
				this.pindan_id = pindan_id;
			},
			onSelectShare(option) {
				alert(option.name)
			},
			onLoad() {
				var that = this;
				if(!that.$route.query.sid) {
					that.$toast('参数错误');
					return false;
				} else {
					that.sid = that.$route.query.sid
				}
				if(!that.$route.query.id) {
					that.$toast('参数错误');
					return false;
				} else {
					that.id = that.$route.query.id;
				}
				that.onGetGoods(true);
			},

			onGetGoods(isFirst) {
				var that = this;
				var params = {
					sid: that.sid,
					id: that.id,
					menufooter: 1
				};
				if(isFirst) {
					params['is_first'] = 1;
				}
				that.util.request({
					url: 'wmall/store/goods/newDetail',
					data: params,
				}).then((res) => {
					that.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						that.$toast(result.message);
						return;
					}
					result = result.message;
					var goodsActive = result.goodsDetail;
					that.goodsActive = goodsActive;
					that.cart = result.cart.message.cart;
					that.store = result.store;
					that.replaceStore(result.store);
					that.replaceCart(result.cart.message.cart);
					that.util.setWXTitle(goodsActive.title);
					if(isFirst) {
						that.videoList = [...result.videoList];
						that.goodsIndex = result.goodsIndex;
						that._last = result.goodsIndex;
						that.menufooter = window.menufooter;
					}
				})
			},

			onSwipeChange(index) {
				var that = this;
				that.id = that.videoList[index].id;
				that.goodsIndex = index;
				var last = that._last;
				var goods = that.videoList[last];
				if(goods.showType == 'video') {
					var key = 'videoPlayer-' + last;
					var videoDom = that.$refs[key][0];
					var video = videoDom.player.tech_.el_;
					video.pause();
				}
				that._last = index;
				that.onGetGoods();
				var length = that.videoList.length;
				if(index == length - 1) {
					that.$toast('已加载所有商品');
				}
			},

			onFavor() {
				var that = this;
				if(that.store.is_favorite) {
					var type = 'cancal';
				} else {
					var type = 'star';
				}
				that.util.request({
					url: 'wmall/member/op/favorite',
					data: {
						id: that.store.id,
						type: type
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						that.$toast(result.message);
						return;
					}
					if(type == 'star') {
						that.$toast('添加收藏成功');
						that.store.is_favorite = 1;
					} else {
						that.$toast('取消收藏成功');
						that.store.is_favorite = 0;
					}
				})
			},

			onPlayerReady(index) {
				var key = 'videoPlayer-' + index;
				var videoDom = this.$refs[key][0];
				var video = videoDom.player.tech_.el_;
				if(this.util.isIos()) {
					video.removeAttribute('x5-video-player-type', 'h5-page')
					video.setAttribute('playsinline', true)
					video.setAttribute('x5-playsinline', true)
					video.setAttribute('webkit-playsinline', true)
				} else {
					video.setAttribute('x5-video-player-type', 'h5-page')
					video.removeAttribute('playsinline')
					video.removeAttribute('x5-playsinline')
					video.removeAttribute('webkit-playsinline')
				}
				video.play();
			},
		},

		computed: {
			...mapState([
				'istore', 'icart',
			]),
		},
		created(){
			this.query = this.$route.query;
			if(this.query) {
				this.pindan_id = 0;
				if(this.query.pindan_id > 0) {
					this.pindan_id = this.query.pindan_id;
				}
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#goods-video .van-share-sheet{
		z-index: 10000000 !important;
	}
	#goods-video .van-nav-bar{
		background-color: #000!important;
	}
	#goods-video .van-tabbar:after{
		border-top-width: .1px!important;
		border-bottom-width: 0px!important;
	}
	#goods-video .van-tabbar,
	#goods-video .van-tabbar-item--active
	{
		background-color: transparent!important;
	}
	#goods-video .van-tabbar-item,
	#goods-video .van-tabbar-item__text span
	{
		color: #fff!important;
	}
	#goods-video .content{
		background-color: #000;
		bottom: 0px;
	}
	#goods-video .goods-swipe,
	#goods-video .slide-swipe,
	#goods-video .thumb-wrap
	{
		width: 100%;
		height: 100%;
	}
	#goods-video .slide-swipe .van-swipe-item,
	#goods-video .thumb-wrap
	{
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#goods-video .video-player,
	#goods-video .video-player .video-js
	{
		width: 100%;
		height: 100%;
	}
	#goods-video .video-player .video-js video{
		object-fit: fill;
	}
	#goods-video .vjs-control-bar{
		display: none;
	}
	#goods-video .goods-info{
		position: absolute;
		z-index: 1;
		right: 0;
		bottom: 20px;
		left: 0;
		line-height: 1;
		padding: 0 55px 0 15px;
		color: #fff;
		font-size: 14px;
	}
	#goods-video #public-footer~.content .goods-info{
		bottom: 70px;
	}
	#goods-video .goods-info .goods-title{
		line-height: 1.5;
		font-size: 16px;
		font-weight: bold;
		color: #ff2d4b;
	}
	#goods-video .goods-info .goods-desc,
	#goods-video .goods-info .sailed
	{
		line-height: 1.5;
	}
	#goods-video .goods-info .price{
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-end;
		margin-top: 15px;
	}
	#goods-video .goods-info .price .old-price{
		text-decoration: line-through;
		margin-left: 10px;
	}
	#goods-video .btn-group{
		position: absolute;
		z-index: 2;
		right: 0;
		bottom: 80px;
		padding-right: 10px;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		line-height: 1;
	}
	#goods-video #public-footer~.content .btn-group{
		bottom: 130px;
	}
	#goods-video .btn-group .store-logo{
		position: relative;
		margin-bottom: 5px;
	}
	#goods-video .btn-group .store-logo .logo{
		width: 44px;
		height: 44px;
		padding: 1px;
		border: 1px solid #fff;
		border-radius: 50%;
		box-sizing: border-box;
	}
	#goods-video .btn-group .store-logo .logo img{
		border-radius: 50%;
		width: 100%;
		height: 100%;
	}
	#goods-video .btn-group .store-logo .icon{
		position: absolute;
		z-index: 1;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 40%);
		font-size: 22px;
		color: #ff2d4b;
	}
	#goods-video .btn-group .btn-item{
		position: relative;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		color: #fff;
		margin-top: 20px;
	}
	#goods-video .btn-group .btn-item .icon{
		font-size: 36px;
	}
	#goods-video .btn-group .btn-item .icon-cart{
		width: 44px;
		height: 44px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #ff2d4b;
		border-radius: 100px;
	}
	#goods-video .btn-group .btn-item .icon-roundaddfill{
		color: #ff2d4b;
	}
	#goods-video .btn-group .btn-item .btn-text{
		font-size: 11px;
		margin-top: 5px;
	}
	#goods-video .btn-group .btn-item .goods-num{
		position: absolute;
		right: -7px;
		top: -7px;
		padding: 3px;
		min-width: 17px;
		text-align: center;
		box-sizing: border-box;
		border-radius: 50px;
		font-size: 11px;
		background-color: #ff2d4b;
		color: #fff;
	}
	#goods-video #goods-handle .foodop{
		z-index: 10;
		right: 15px;
		bottom: 20px;
	}
	#goods-video #public-footer~.content #goods-handle .foodop{
		bottom: 70px;
	}
	#goods-video #goods-handle .foodop .num{
		color: #fff;
	}

</style>


