<template>
	<div id="i-van-share-sheet">
		<van-share-sheet
			v-model="popupStatus"
			:options="options"
			:title="title"
			@select="onSelectShare"
			@cancel="onCancelShare"
			@click-overlay="onCancelShare"
			:close-on-click-overlay="true"
		/>
		<div class="share-zhezhao" @click="onChangeZhezhao(false)" v-if="zhezhaoShow">
			<img src="static/img/share-layer.png" alt="">
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			title: {
				type: String,
				default() {
					return '立即分享给好友'
				}
			},

			cancelext: {
				type: String,
				default() {
					return '取消'
				}
			},
			
			options: {
				type: Array,
				default() {
					return [
						{name: '微信', icon: 'wechat'},
						{name: '分享海报', icon: 'poster'}
					]
				}

			},

			status: Boolean,

			query: {
				type: Object,
				default() {
					return {
						type: 0,
						id: 0,
					}
				}
			}
		},
		data() {
			return {
				url: window.location.href,
				zhezhaoShow: false,
			}
		},
		computed: {
			popupStatus: {
				get(){
					return this.status
				},
				set(val){}
			}
		},
		methods: {
			onSelectShare(option) {
				if(option.icon == 'wechat') {
					this.onChangeZhezhao(true);
				} else if(option.icon == 'poster') {
					let posterUrl = this.util.getUrl({path: '/plugin/pages/poster/index', query: this.query});
					this.$router.push(this.util.getUrl(posterUrl));
				}
			},

			onCancelShare() {
				this.$emit('onTogglePopup');
			},

			onTogglePopup() {
				this.$emit('onTogglePopup');
			},

			onChangeZhezhao(force) {
				if(force) {
					this.onTogglePopup();
				}
				this.zhezhaoShow = !this.zhezhaoShow;
			},

			onCopy(e) {
				this.onTogglePopup();
				this.util.$toast('复制成功')
			},

			onError: function (e) {
				this.util.$toast('复制失败')
			}
		}
	}
</script>

<style>
	#poster-popup{
		position: fixed;
		z-index: 100000;
	}
	#poster-popup .van-col{
		text-align: center;
		font-size: 13px;
		color: #7a7a7a;
		padding: 40px 0 20px;
	}
	#poster-popup .van-popup img{
		width: 60px;
		height: 60px;
	}
	#poster-popup .icon-close{
		position: absolute;
		top: 10px;
		right: 10px;
		font-size: 18px;
		color: #7a7a7a;
	}
</style>


